body {
    min-width: 350px;
}

.topbar {
    background-color: #004170;
}

.topbar_link {
    color: #ffff !important;
    font-size: 18px !important;
}

.topbar_link:hover {
    color: #f1eaea !important;
}

.slick-arrow {
    display: none !important;
}

#img_Loader {
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    height: 100vh;
}

#Footer ul {
    list-style: none;
}

#Footer li {
    margin-top: 10px;
}

#Footer ul li a {
    color: rgb(0, 0, 0) !important;
    text-decoration: none !important;
}

#Footer ul li a:hover {
    color: rgb(189, 143, 19) !important;
    font-style: italic;
}

.footer_social_icons a {
    color: black;
    font-size: 20px;
}
.footer_social_icons a:hover {
    color: rgb(228, 130, 2);
}

@media screen and (min-width: 768px) {
    .site_body {
        width: 85%;
        margin: auto !important;
    }

    .home_cards_description {
        max-height: 100px;
        min-height: 100px;
        height: 100px !important;
    }

    #findretailer_input {
        width: 75% !important;
        height: 45px;
    }

}

@media screen and (max-width: 900px) {
    .topbarLinks_text {
        display: none !important;
    }
}